html {
  box-sizing: border-box;
}


*,
*::before,
*::after {
  box-sizing: inherit;
}

button {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  padding: 0;
  margin: 0;
  min-width: 320px;
  height: 100%;
  font-size: 20px;
  font-family: var(--font-family-primary);
  color: var(--color-primary-darker);
  line-height: var(--leading-primary);
  padding-top: var(--header-height);

  @media screen and (max-width: 992px) {
    font-size: 16px;
  }

  @media screen and (max-width: 767px) {
    padding-top: 0;
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  height: 100%;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 1fr auto; // change to auto 1fr auto if header is not absolute
}

.is-hidden {
  display: none !important;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}



.container {
  margin: 0 auto;
  max-width: var(--container-width);
  padding: 0 var(--container-inline-paddings);
}





.ibg {
  position: relative;
}

.ibg img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}


.global-wrapper {
  position: relative;
}


.locked {
  overflow: hidden;
}